.scrollbar::-webkit-scrollbar-track
{
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
          box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
  border-radius: 5px;
  background-color: #0a3461;
}

.scrollbar::-webkit-scrollbar
{
	width: 10px;
  background-color: linear-gradient(to bottom, #338EEA, #0a346100);
  border-radius: 5px;
}

.scrollbar::-webkit-scrollbar-thumb
{
  background-color: transparent;
  border-radius: 5px;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#135aa7), color-stop(65%, #135aa7), to(#338EEA00));
  background-image: -webkit-linear-gradient(top, #135aa7, #135aa7 65%, #338EEA00);
  background-image: linear-gradient(to bottom, #135aa7, #135aa7 65%, #338EEA00);
}

.scrollbar-small::-webkit-scrollbar-track
{
  -webkit-box-shadow: inset 0 0 3px rgba(0,0,0,0.3);
          box-shadow: inset 0 0 3px rgba(0,0,0,0.3);
  border-radius: 2px;
  background-color: #0a3461;
}

.scrollbar-small::-webkit-scrollbar
{
	height: 6px;
  background-color: linear-gradient(to right, #338EEA, #0a346100);
  border-radius: 2px;
}

.scrollbar-small::-webkit-scrollbar-thumb
{
  background-color: transparent;
  border-radius: 2px;
  background-image: -webkit-gradient(linear, left top, right top, from(#135aa7), color-stop(65%, #135aa7), to(#338EEA00));
  background-image: -webkit-linear-gradient(left, #135aa7, #135aa7 65%, #338EEA00);
  background-image: linear-gradient(to right, #135aa7, #135aa7 65%, #338EEA00);
}


