.radar {
  background: radial-gradient(center, #338EEA60 0%, #0a346100 75%),
              repeating-radial-gradient(#0a346100 5.8%, #0a346100 18%, #338EEA 18.6%, #0a346100 18.9%),
              -webkit-gradient(linear, left top, right top, color-stop(49.5%, #0a346100), color-stop(50%, #338EEA), color-stop(50%, #338EEA), color-stop(50.2%, #0a346100)),
              -webkit-gradient(linear, left bottom, left top, color-stop(49.5%, #0a346100), color-stop(50%, #338EEA), color-stop(0%, #338EEA), color-stop(50.2%, #0a346100));
  background: -webkit-radial-gradient(center, #338EEA60 0%, #0a346100 75%),
              -webkit-repeating-radial-gradient(#0a346100 5.8%, #0a346100 18%, #338EEA 18.6%, #0a346100 18.9%),
              -webkit-linear-gradient(left, #0a346100 49.5%, #338EEA 50%, #338EEA 50%, #0a346100 50.2%),
              -webkit-linear-gradient(bottom, #0a346100 49.5%, #338EEA 50%, #338EEA 0%, #0a346100 50.2%);
  background: -o-radial-gradient(center, #338EEA60 0%, #0a346100 75%),
              -o-repeating-radial-gradient(#0a346100 5.8%, #0a346100 18%, #338EEA 18.6%, #0a346100 18.9%),
              -o-linear-gradient(left, #0a346100 49.5%, #338EEA 50%, #338EEA 50%, #0a346100 50.2%),
              -o-linear-gradient(bottom, #0a346100 49.5%, #338EEA 50%, #338EEA 0%, #0a346100 50.2%);
  background: radial-gradient(center, #338EEA60 0%, #0a346100 75%),
              repeating-radial-gradient(#0a346100 5.8%, #0a346100 18%, #338EEA 18.6%, #0a346100 18.9%),
              linear-gradient(90deg, #0a346100 49.5%, #338EEA 50%, #338EEA 50%, #0a346100 50.2%),
              linear-gradient(0deg, #0a346100 49.5%, #338EEA 50%, #338EEA 0%, #0a346100 50.2%);
  /* position: relative; */
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  border-radius: 50%;
  border: 0.2rem solid #338EEA;
  overflow: hidden;

  width: calc( 100% - 10px );
  height: calc( 100% - 10px );
  position: absolute;
  left: 50%;
  top: 50%;
  /* background: linear-gradient(45deg, #fff, #fff 10%, #ffffff00 25%); */
  -webkit-transform-origin: 0% 0%;
      -ms-transform-origin: 0% 0%;
          transform-origin: 0% 0%;
  -webkit-animation: rotation 20s infinite linear;
          animation: rotation 20s infinite linear;
}
.radar-scanner:after {
  content: ' ';
  display: block;
  background-image: -webkit-linear-gradient(46deg, rgba(0, 255, 51, 0) 50%, #338EEA 100%);
  background-image: -o-linear-gradient(46deg, rgba(0, 255, 51, 0) 50%, #338EEA 100%);
  background-image: linear-gradient(44deg, rgba(0, 255, 51, 0) 50%, #338EEA 100%);
  width: 50%;
  height: 50%;
  position: absolute;
  top: 0;
  left: 0;
  -webkit-animation: radar-beam 5s infinite;
          animation: radar-beam 5s infinite;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-transform-origin: bottom right;
      -ms-transform-origin: bottom right;
          transform-origin: bottom right;
  border-radius: 100% 0 0 0;
}

@-webkit-keyframes radar-beam {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes radar-beam {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


